import { Component, OnInit, ViewChild, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { Subscription, Subject } from 'rxjs';

import { MatDrawer } from '@angular/material/sidenav';

import { SharedService } from 'src/app/shared/services/shared.service';


@Component({
  selector: 'main-program',
  templateUrl: './main-program.component.html',
  styleUrls: ['./main-program.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class MainProgramComponent implements OnInit {

  // Private
  private _unsubscribeAll: Subject<any>;

  isDataLoaded:boolean = true;

  prgramData:string = "";

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';

  _programsList:any;
  fullProgramsList:any = [];

  searchData:FormControl;

  search:string = '';
  apiSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private sharedService:SharedService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this._programsList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

  }

  get programs$(): Observable<any>{
    return this._programsList.asObservable();
  }

  onBackdropClicked() {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    // Go to the parent route
    this._router.navigate(['.'], {relativeTo: this._activatedRoute.parent});

    this.matDrawer.close();

    // Mark for check
    this._changeDetectorRef.markForCheck();

  }

  addMainProgram() {

    console.log(2);

    // Get the current activated route
    let route = this._activatedRoute;

    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    var id = this.sharedService.guid();

    this.sharedService.setMatDrawerId(id);
    this._router.navigate([this._router.url+'/', 'new', id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  getIndexParams(event){

    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;

  }

  onGlSearchFilter(event) {
    console.log(event)
		if (event != "") {
      let newData = this.allData.filter(item => {
        for (let field in item) {
          if (item[field] === null || item[field] === undefined) {
            continue;
          }
          if (item[field].toString().toLowerCase().indexOf(event.toString().toLowerCase()) !== -1) {
            return item;
          }
        }
      })

      this._programsList.next(newData);
		} else {
      this.fullProgramsList = this.allData;
      this._programsList.next(this.fullProgramsList);
    }

	}


  ngOnInit(): void {

    // Load programs
    let res = [
      {
        "id": 107,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Dance",
        "description": "some dance related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 108,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Yoga",
        "description": "some yoga related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 109,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Karate",
        "description": "some karate related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      }

    ];

    this.fullProgramsList = res;
    this.allData = res;
    this._programsList.next(res);

    this.programs$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    });

    // Subscribe to search input field value changes
    this.searchData.valueChanges
    .pipe(
      startWith(''),
      map((val) => {

        let newData = this.fullProgramsList.filter(item => {
          for (let field in item) {
            if (item[field] === null || item[field] === undefined) {
              continue;
            }
            if (item[field].toString().toLowerCase().indexOf(val.toString().toLowerCase()) !== -1) {
              return item;
            }
          }
        })

        this._programsList.next(newData);


      })
    )
    .subscribe();

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }

}
