import { Component, OnInit, ViewEncapsulation, ViewChild, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Subject } from 'rxjs';

import { MatDrawerToggleResult } from '@angular/material/sidenav';
import { MainProgramComponent } from 'src/app/modules/admin/program/program-setup/main-program/main-program.component';

import { SharedService } from 'src/app/shared/services/shared.service';


@Component({
  selector: 'app-add-main-program',
  templateUrl: './add-main-program.component.html',
  styleUrls: ['./add-main-program.component.scss']
})
export class AddMainProgramComponent implements OnInit {

  isProgramSubmitted:boolean = true;
  isEdit:boolean = false;

  category:any;
  categoryName:any;

  program:any;

  private _unsubscribeAll: Subject<any>;

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _mainProgramComponent: MainProgramComponent,
    private sharedService:SharedService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

  }

  closeDrawer(): Promise<MatDrawerToggleResult>{
    return this._mainProgramComponent.matDrawer.close();
  }

  goBack() {
    this._mainProgramComponent.matDrawer.close();
    this._router.navigate(['.'], {relativeTo: this._activatedRoute.parent});
  }

  submitMainProgramForm(form) {

  }

  ngOnInit(): void {

    this.program = {

    }

    //Open the drawer
    this.sharedService.matdraweridcast.subscribe(id => {

      if(id != null && id == this._activatedRoute.params['value'].id){
        this._mainProgramComponent.matDrawer.open();
      }

      //Mark for check
      this._changeDetectorRef.markForCheck();

    })

  }

}
