import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { MainProgramComponent } from './main-program.component';
import { AddMainProgramComponent } from './add-main-program/add-main-program.component';
import { MainProgramResolver } from './main-program.resolver';


const routes: Routes = [
  { path: '', component: MainProgramComponent, canActivate: [AuthGuard],
    children: [
      {
        path         : ':type/:id',
        component    : AddMainProgramComponent,
        canActivate: [AuthGuard],
        resolve: {
          category: MainProgramResolver
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MainProgramRoutingModule { }



